.card-header {
    display: flex;
    padding: 16px 16px 0;
    border-radius: 10px;
    flex-direction: column;
    border: 1px solid var(--gray2);

    &__inner {
        flex: 1 0 33%;
    }

    &__item {
        margin-bottom: 16px;

        p:first-child {
            color: var(--gray);
            margin-bottom: 4px;
        }

        &__flex {
            display: flex;
            margin-bottom: 8px;

            >.input {
                width: 224px;
                margin-right: 16px;
            }
        }
    }

    &__wrapper {
        display: flex;
        flex: 1
    }

    &__bottom {
        border-top: 1px solid var(--gray2);
        padding-bottom: 16px;
    }

    &__files {
        display: flex;
    }

    &__file {
        margin-right: 12px;
    }
}

.card {
    display: flex;
    flex-direction: column;
    padding: 16px;
    border-radius: 10px;
    border: 1px solid var(--gray2);

    &__file {
        margin-top: 8px;
    }

    &__text {
        margin-top: 8px;
        color: var(--gray)
    }
}