.crumbs {
    color: var(--gray1);
    margin-bottom: 16px;
    font-size: 13px;
    line-height: 16px;

    &__wrap {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    &__item {
        display: inline;
        position: relative;
    }

    &__item::after {
        content: "/";
    }

    &__item:last-child::after {
        display: none;
    }
}

.crumbs a.crumbs__link {
    color: var(--gray);
}

.crumbs a.crumbs__link:hover {
    color: var(--dark);
}