.search {
    height: 40px;
    border: 1px solid var(--gray1);
    border-radius: 6px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 8px;

    &__input {
        flex: 1;
        border: 0;
        font-size: 15px;
        line-height: 20px;

        &::placeholder {
            color: var(--gray1);
        }
    }

}