.table {
    border: 1px solid var(--gray2);
    border-radius: 10px;
    overflow: hidden;

    &__header {
        height: 60px;
        display: flex;
        align-items: center;
        padding: 0 16px;
        border-bottom: 1px solid var(--gray2);
    }

    &__title {
        font-size: 17px;
        font-weight: 600 !important;
        line-height: 20px;
        letter-spacing: -0.4px;
        margin-right: 36px !important;
    }

    &__count {
        flex: 1;
        display: flex;
        justify-content: flex-end;
    }
}