.header {
    width: 100%;
    background: var(--white);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
    height: 56px;
    display: flex;
    align-items: center;
    padding: 0 30px;
    user-select: none;

    &__logo {
        width: 157px;
        max-width: 100%;
        margin-right: 60px;
    }

    &__nav {

        >a,
        button {
            margin-right: 8px;
        }
    }

    &__user {
        flex: 1;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        >button {
            margin-left: 16px;
        }
    }
}