.button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    max-width: 100%;
    min-height: 40px;
    background: var(--base);
    border-radius: 6px;
    border: 1px solid transparent;
    padding: 4px 16px;
    font: 500 13px/16px var(--font);
    letter-spacing: 0.08px;
    color: var(--white);
    cursor: pointer;
    transition: 0.25s;

    &__icon {
        flex-shrink: 0;
        width: 24px;
        margin-right: 6px;
        display: flex;
        align-items: center;
    }

    &__text {
        max-width: 100%;
    }

    &_full {
        width: 100%;
    }


    &_small {
        min-height: 32px;
        font-size: 13px;
        line-height: 16px;
        letter-spacing: -0.08px;
    }

    &_large {
        min-height: 48px;
        font-size: 15px;
        line-height: 20px;
        letter-spacing: -0.2px;
    }

    &_outlined {
        background: transparent;
        color: var(--base);
        border-color: var(--gray1);
    }

    &:hover {
        background: var(--dark);
        color: var(--white);
    }

    &_second {
        background: var(--minor);
        color: var(--base);

        &:hover {
            background: var(--gray2);
            color: var(--base);
        }
    }
}