@import-normalize;

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 100 900;
  font-display: swap;
  src: url(../assets/fonts/Inter.ttf) format('opentype');
}

* {
  box-sizing: border-box;
  text-decoration: none;
  word-break: break-word;
}

*:hover,
*:focus {
  outline: 0;
}

body {
  background: #F1F1F1;
  font: 400 17px/24px var(--font);
  letter-spacing: -0.4px;
  color: var(--base);
}

body::-webkit-scrollbar {
  width: 10px;
  height: 0;
  background: var(--white);
}

body::-webkit-scrollbar-thumb {
  width: 10px;
  background: var(--gray);
  border-radius: 10px;
}

body.hidden {
  overflow: hidden;
}

@supports not selector(::-webkit-scrollbar) {
  body {
    scrollbar-width: thin;
    scrollbar-color: var(--gray) var(--white);
  }
}

#app {
  overflow: hidden;
  width: 100%;
  max-width: 1440px;
  min-width: 1210px;
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  background: var(--white);
}


.overflowH {
  overflow: hidden;
}

.textGray {
  color: var(--gray);
}

.subhead {
  font: 400 15px/20px var(--font);
  letter-spacing: -0.2px;
}

.subheadM {
  font: 500 15px/20px var(--font);
  letter-spacing: -0.2px;
}

a {
  color: var(--base);
  transition: 0.25s;
}

a:hover {
  color: var(--dark);
}

button {
  border: none;
  background: none;
  margin: 0;
  padding: 0;
  font: 500 13px/1.23 var(--font);
  color: var(--base);
  cursor: pointer;
  transition: 0.25s;
}

img {
  width: auto;
  height: auto;
  max-width: 100%;
}

svg {
  transition: 0.25s;
}

strong {
  font-weight: 600;
}