.input {
    &__label {
        margin-bottom: 4px;

        &_required {
            &:after {
                content: '*';
                color: var(--red);
                margin-left: 2px;
            }
        }
    }

    &__area {
        height: 44px;
        border: 1px solid var(--gray1);
        border-radius: 6px;
        display: flex;
        overflow: hidden;
    }

    &__text-area {
        height: 100%;
        flex: 1;
        border: 0;
        padding: 0 12px;
        font-size: 15px;
        line-height: 20px;
        letter-spacing: -0.2px;
    }
}